@import "variable";

//红色头部标题，无返回和编辑图标（我的联盟 我的团队 新推客 店铺等）
.jm-header-navbar {
  height: 1rem;
  background: #f23030;
  font-size: $font-size1; //.36rem;
  padding-top: .2rem;
  color: #fff;
  .jm-wrapper {
    position: relative;
    height: .8rem;
    line-height: .8rem;
    .title {
      height: .8rem;
      line-height: .8rem;
      text-align: center;
      font-size: $font-size1; //.36rem;
      color: #fff;
    }
    .jm-back {
      display: none;
      position: absolute;
      left: .16rem;
      top: .17rem;
      width: .3rem;
      height: .35rem;
