//头部导航固定在上部
.jm-header.header-fixed {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 2;//原来100
}

//头部导航固定间隙占位符
.header-fixed + .hold-space-top {
  height: 1.26rem;
}

.header-fixed ~ .page-content .hold-space-top {
  height: 1.26rem;
}



