//返回头部 begin
.jm-header {
  background: #fff;
  height: 1.26rem;
  padding-top: .26rem;
  position: relative;
  width: 7.5rem;
  max-width: 1080px;

  .jm-wrapper {
    position: relative;
    z-index: 1;
    .title {
      height: 1rem;
      line-height: 1rem;
      text-align: center;
      font-size: .31rem;
      color: #000;
    }
    .posl, .posr, .poslike {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }
    .posl {
      left: .24rem;
    }
    .posr {
      right: .24rem;
    }
    .poslike {
      right: 1rem;
    }
    &::after {
      border-bottom: 1px solid #E6E6E6;
      bottom: 0;
      content: "";
      height: 1px;
      position: absolute;
      right: 0;
      transform: scaleY(.8);
      width: 100%;
      z-index: 10;
    }
    &.un-bottom-line::after {
      border-bottom: none;
    }
    a [class^="icon-"], a [class*=" icon-"] {
      display: inline-block;
    }
  }
}
//头部背景透明，移除底部垂直空间
.transparent-bg.header-fixed + .hold-space-top, .hold-space-top {
  height: 0;
}
//头部100%背景透明
.transparent100{
  background:transparent !important;
  opacity:1 !important;
}
//背景透明  添加透明图  去除底线
.transparent-bg {
  &.header-fixed {
    background:rgb(255,255,255);//白色透明度
    opacity:.3;
    min-height: 1.26rem;;
  }
  .jm-wrapper {
    &::after {
      border-bottom-width: 0px;
    }
    &.bottom-line::after {
      border-bottom-width: 1px;
    }
  }
  &.bottom-line .jm-wrapper {
    &::after {
      border-bottom-width: 1px;
    }
  }
}
.nav-none .icon_header-nav-short{
  display: none;
}
//弹出层导航栏
.header-nav-bar{
  display: none;
  background: rgba(0, 0, 0, .6);
  position: fixed;
  top: 1.2rem;
  right: .1rem;
  width: 120px;
  height: auto;
  z-index: 101;
  border-radius: 8px;
  padding: 0px 12px;
  .triangle {
    display: inline-block;
    width: 0px;
    height: 0px;
    border-width: 0px 7px 12px 7px;
    border-style: solid solid solid solid;
    border-color: transparent;
    border-bottom-color: rgba(0, 0, 0, .6);
    position: absolute;
    top: -12px;
    right: 12px;
  }
  .navltem {
    height: 40px;
    line-height: 40px;
    color: #c9caca;
    font-size: 12px;
    position: relative;
    padding-left: 40px;
    vertical-align: middle;
    .navicon {
      display: inline-block;
      width: 30px;
      height: 100%;
      position: absolute;
      top: 50%;
      left: 0px;
      transform: translateY(-50%);
    }
  }

}

.icon_header_nav_home {
  background: url("../images/common/icon_header_nav_home.png") no-repeat center center;
  background-size: 16px auto;
}

//底部导航
.app-page-footer {
  height: 1rem;
  display: block;
  overflow: hidden;
  position: relative;
  .footer_inner {
    max-width: 1080px;
    width: 100%;
    background-color: #fff;
    bottom: 0;
    height: 1rem;
    position: fixed;
    z-index: 9990;
    //border-top: 1px solid #e3e3e3;
    a {
      display: block;
      float: left;
      font-size: 0.24rem;
      height: 1rem;
      line-height: 1rem;
      text-align: center;
      width: 25%;
      .icon {
        display: inline-block;
        width: .5rem;
        height: .5rem;
        position: absolute;
        top: 0.1rem;
        left: 50%;
        transform: translate(-50%, 0);
      }
      .line {
        display: block;
        position: relative;
        height: 0.6rem;
      }
      .text {
        display: block;
        height: 0.4rem;
        line-height: 0.4rem;
        color: #595757;
      }
      &.on .text {
        color: #ff6b00;
      }
      .icon-invite {
        background: url("../images/app/icon/icon_invite_normal.png") no-repeat center center;
        background-size: .46rem .45rem;
      }
      &.on .icon-invite {
        background: url("../images/app/icon/icon_invite_Pressed.png") no-repeat center center;
        background-size: .46rem .45rem;
      }
      .icon-team {
        background: url("../images/app/icon/icon_team_normal.png") no-repeat center center;
        background-size: .44rem .45rem;
      }
      &.on .icon-team {
        background: url("../images/app/icon/icon_team_Pressed.png") no-repeat center center;
        background-size: .44rem .45rem;
      }

      .icon-alliance {
        background: url("../images/app/icon/icon_ alliance_normal.png") no-repeat center center;
        background-size: .58rem .45rem;
      }
      &.on .icon-alliance {
        background: url("../images/app/icon/icon_alliance_Pressed.png") no-repeat center center;
        background-size: .58rem .45rem;
      }
      .icon-my {
        background: url("../images/app/icon/icon_my_normal.png") no-repeat center center;
        background-size: .21rem .45rem;
      }
      &.on .icon-my {
        background: url("../images/app/icon/icon_my_Pressed.png") no-repeat center center;
        background-size: .21rem .45rem;
      }
    }
  }
}

