.listbox {
  color: #595757;
  .itembox {
    background-color: #fff;
    padding: 0.2rem .3rem 0.2rem .4rem;
    margin: 0 0 .28rem 0;
    .item {
      position: relative;
      line-height: .6rem;
      height: .6rem;
      overflow: hidden;
      .left {
        float: left;
        color: $font-color2; //231815
        font-size: .24rem;
      }
      .right {
        float: right;
        right: 0;
        @include vertical-center;//垂直居中
      }
      .time {
        color: $font-color5; //#9FA0A0;
        font-size: .24rem;
      }
      .phone {
        font-size: .26rem;
        color: $font-color2; //231815
      }
      .state {
        @include yellow-bg-button;
      }
    }
    .done {
      text-align: center;
      .btn {
        display: inline-block;
        width: 2.6rem;
        height: .54rem;
        line-height: .54rem;
        background-color: #F23030;
        border-radius: 7px;
        color: #fff;
        font-size: .26rem;
        text-align: center;
        text-shadow: none;
      }
    }
  }
}
