//品牌商品，商品分类 ，详情商城搜索结果，有热销，分享 商品框白底 4.74rem 高begin ,
.brandgoods {
  background: #f4f4f4;
  color: #231816;
  .goods-box {
    display: flex;
    flex-direction: row;
    width: 6.96rem;
    margin: 0.26rem .28rem .28rem .28rem;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
    flex-wrap: wrap;
    .goods-item {
      flex-basis: 3.37rem;
      flex-grow: 0;
      flex-shrink: 1;
      height: 4.74rem;
      background-color: #fff;
      margin-bottom: .2rem;
