      .imginfo {
        position: relative;
        overflow: hidden;
        padding-top: 100%;

      }
      .imgbox{
        position: absolute;
        width:100%;
        left: 0;
        top:0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        padding: .2rem;
        img{
          display: inline-block;
          max-width: 100%;
        }
